.carousel-block {
  margin: 120px auto;
  position: relative;
  width: 768px; }
  .carousel-block .carousel-slider {
    margin-bottom: 24px; }
  .carousel-block .carousel-controls {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 100%; }



